<template>

<div class="page contactPage">
  <NavBar></NavBar>
  <MainTitle title="Kontakt"></MainTitle>

  <div class="contactWrapper">

    <form id="app">

      <div v-if="errors.length">
        <b>Please correct the following error(s):</b>
        <ul>
          <template v-for="(error, index) in errors">
            <li v-bind:key="index">{{ error }}</li>
          </template>
        </ul>
      </div>

      <div class="crow">
        <label for="fname">Vorname</label>
        <input id="fname" v-model="vorname" type="text" name="fname">
      </div>

      <div class="crow">
        <label for="lname">Nachname</label>
        <input id="lname" v-model="nachname" type="text" name="lname">
      </div>

      <button v-on:click="sendMail">Send Mail</button>
    </form>



  </div>

  <InstaFeed></InstaFeed>
  <MainFooter></MainFooter>
</div>

</template>

<script>

import NavBar from "@/components/navBar/NavBar";
import MainTitle from "@/components/titles/MainTitle";
import MainFooter from "@/components/footer/MainFooter";
import InstaFeed from "@/components/socialMedia/InstaFeed";
import axios from "axios";

export default {
  name: "ContactPage",
  components: {
    InstaFeed,
    MainFooter,
    NavBar,
    MainTitle
  },
  data() {
    return {
      errors: [],
      vorname: null,
      nachname: null,
      name: null,
      age: null,
      movie: null
    }
  },
  methods: {
    checkForm: function (e) {
      if (this.name && this.age) {
        return true;
      }

      this.errors = [];
      if (!this.name) {
        this.errors.push('Name required.');
      }
      if (!this.age) {
        this.errors.push('Age required.');
      }
      e.preventDefault();
    },
    sendMail: function() {

      /*
       {
         vorname:"Johnny",
         nachname: "Depp",
         email: "jdepp@test.de",
         telefon: "12345",
         hochzeitsdatum: "25.12.22",
         hochzeitsort: "München",
         service: "Foto",
         hochzeitsstil: "Glam",
         nachricht: "Lorem Ipsum Bla Bla",
         gefunden: "Empfehlung"
      }
      */

      axios.post('http://127.0.0.1:5000/send', {
        vorname: this.vorname,
        nachname: this.nachname
      })
      .then(function (response) {
        alert(response.data);
        console.log(response);
      })
      .catch(function (error) {
        alert("ERROR!");
        console.log(error);
      });
    }
  },
}

</script>

<style scoped>

.contactWrapper {
  width: 80%;
  margin: 0 auto;
  padding: 50px 0;
  display: flex;
  justify-content: space-evenly;
  background-color: #b5aba7;
}

form {
  width: 80%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.crow {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
  width: 100%;
  margin: 0 0 30px 0;
}

label {
  width: 100%;
  padding: 20px 0 10px 0;
  text-align: left;
  font-size: 20px;
}

input {
  margin: auto;
  width: 100%;
  border: none;
  border-bottom: 1px solid black;
  background-color: #b5aba7;
  font-size: 20px;
  transition: 0.5s;
}

input:hover {
  background-color: #f2eeea;
  transition: 0.5s;
}

input[type=text]:focus {
  background-color: #f2eeea;
  -webkit-transition: 0.5s;
  transition: 0.5s;
  outline: none;
}

button {
  border: none;
  padding: 20px 0;
  cursor: pointer;
  width: 100%;
  border-radius: 25px;
  background-color: #e0dbd5;
}

button:hover {
  opacity: 0.9;
}

</style>